<template>
  <div></div>
</template>

<script>
export default {
  name: "init-index",

  methods: {
    init() {
      this.fetch();
    },

    fetch() {
      this.$store.dispatch("init/fetch");
    },
  },

  mounted() {
    if (this.auto) {
      this.init();
    }
  },

  props: {
    auto: Boolean,
  },
};
</script>
