
<template>
  <div>
    <h1>开发者模式</h1>

    <el-button type="primary" @click="handleClickSetup">
      打开安装（初始设置）界面
    </el-button>

    <el-button type="primary" @click="handleClickInit"> 执行初始化 </el-button>

    <el-button type="primary" @click="handleClickCleanTrash">
      清空回收站
    </el-button>

    <MyAppInit ref="theAppInit" />
  </div>
</template>

<script>
import MyAppInit from "@/components/init";

export default {
  name: "setup-index",

  components: { MyAppInit },

  methods: {
    handleClickSetup() {
      this.$router.push("/setup");
    },

    handleClickInit() {
      this.$refs.theAppInit.init();
    },

    handleClickCleanTrash() {
      let p = {
        method: "POST",
        type: "trash",
        id: "clean",
        data: {},
      };
      this.$store.dispatch("rest/execute", p);
    },
  },
};
</script>
